import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { useRef, useEffect } from "react";

import "./CategoryVote.scss"
import SectionTitle from "../../../components/SectionTitle"
import haruhi from "../../../assets/haruhi.webp"

gsap.registerPlugin(ScrollTrigger);

function CategoryVote() {

    const categories = [
        "Light Novel c yu thch nht",
        "Light Novel c mong ch nht",
        "Light Novel c yu thch nht",
        "Light Novel c yu thch nht",
        "Light Novel c yu thch nht",
        "Light Novel c yu thch nht",
        "Light Novel c yu thch nht",
    ]

    useEffect(() => {
        const textElements = gsap.utils.toArray('.categoryItem');
        textElements.forEach(text => {
            gsap.fromTo(text, {
                x: 500,
                opacity: 0
            }, {
                x: 0,
                opacity: 1,
                scrollTrigger: {
                    trigger: text,
                    end: 'top 50%',
                    scrub: true,
                },
            });
        })
    }, [])

    const mediaRef = useRef(null);
    useEffect(() => {
        const el = mediaRef.current;
        gsap.fromTo(el, {
            x: -500,
            opacity: 0
        }, {
            x: 0,
            opacity: 1,
            scrollTrigger: {
                trigger: el,
                end: 'top 30%',
                scrub: true,
            },
        });
    }, [])

    return (
        <div className=" wrapper categoryVote">
            <SectionTitle
                title="Hng Mc"
                description="Nhng hng mc bnh chn cho bng xp hng nm nay!"
            />
            <div className="categoryContainer">
                <div className="categoryImg" ref={mediaRef}>
                    <img id="haruhi" alt="" src={haruhi} />
                </div>
                <div className="categoryText">
                    {categories.map((category, index) => (
                        <div className="categoryItem" key={index}>
                            <div className="categoryIndex">0{index + 1}</div>
                            <div className="categoryName">
                                <p>{category}</p>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default CategoryVote;