import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';

import "./Header.scss"
import halumonica from "../../../assets/headerimg.webp"
import Sakura from '../../../components/Sakura';
import rotSakura1 from "../../../assets/sakura1.svg"
import rotSakura2 from "../../../assets/sakura2.svg"

function Header() {
    const [imgHeight, setImgHeight] = useState(null);
    const [shouldHide, setShouldHide] = useState(false);
    const bannerRef = useRef(null);
    const imgRef = useRef(null);

    useEffect(() => {
        const bannerElement = bannerRef.current;
        const imgElement = imgRef.current;

        const handleResize = () => {
            if (imgElement) {
                setImgHeight(bannerElement.clientHeight);
                setShouldHide(window.innerWidth < 900);
            }
        };

        window.addEventListener('resize', handleResize);

        const handleImageLoad = () => {
            if (imgElement) {
                setImgHeight(bannerElement.clientHeight);
            }
        };


        if (imgElement) {
            imgElement.addEventListener('load', handleImageLoad);
        }

        handleResize();

        return () => {
            window.removeEventListener('resize', handleResize);
            if (imgElement) {
                imgElement.removeEventListener('load', handleImageLoad);
            }
        };
    }, []);

    return (
        <div className="header">
            <div className='headerContent' ref={bannerRef}>
                <Sakura density={110} />
                <div className='textContent'>
                    <div className='textTitle'>
                        <h1><span id='text01'>Bng Xp Hng </span><br /><span id='text02'> Light Novel</span><br /><span id='text03'> Vit Nam</span></h1>
                        <img id='rotSakura1' alt='' src={rotSakura1} />
                        <img id='rotSakura2' alt='' src={rotSakura2} />
                    </div>
                    <p>Bn l nh thm him rong rui trong nhng th gii gi tng k b, l nh lnh o ca nhng cuc phiu lu bt tn c thu dt bi nhng con ch!
                        n, v em quyn lc ca bn  bnh chn cho nhng Light Novel c o nht, gp phn lm nn mt bng xp hng xut sc nht lch s Light Novel Vit Nam.
                        Hy l ph thy ca chnh cu chuyn bn yu thch, v hy  ci tn ca cu chuyn  ghi danh bng vng!</p>
                    <div id='textBtn'>
                        <Link id='votebtn' to="/vote" target="_blank">
                            <span>Bnh Chn</span>
                            <img alt='' src={rotSakura1} />
                        </Link>
                    </div>
                </div>
                <div className='imgContent'>
                    <img alt="" src={halumonica} ref={imgRef} />
                </div>
            </div>
            {shouldHide ? null : <div style={{ height: imgHeight }}></div>}
        </div>
    );
}

export default Header;